import constants from './constants';
var initApp = function (dispatch) { return (dispatch({ type: constants.LOAD_NOTES })); };
var controlChanged = function (dispatch, data) {
    dispatch({
        type: constants.LOAD_NOTES,
        data: data
    });
};
var flipFretboard = function (dispatch) {
    dispatch({
        type: constants.FLIP_FRETBOARD
    });
};
var changeTuning = function (dispatch, data) {
    dispatch({
        type: constants.CHANGE_TUNING,
        data: data
    });
};
export { initApp, controlChanged, flipFretboard, changeTuning };
